t========================================================================== */\n\n@use '../../../css/base' as *;\n\na,\nbutton {\n    color: inherit;\n    text-decoration: none;\n    cursor: pointer;\n}\n\nbutton {\n    background-color: transparent;\n    border: 0;\n\n    &:focus {\n        outline: 0;\n    }\n}\n\n/*========================================================================== */\n\n.c-btn {\n    // @include typo_aeonik_14-16;\n    position: relative;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    height: 56px;\n    padding: 20px 32px;\n    font-weight: 400;\n    letter-spacing: 0.03em;\n    border-style: solid;\n    border-width: 1px;\n    border-radius: 30px;\n    transition: background-color 0.25s, border-color 0.25s;\n\n    [class^='icon-'] {\n        display: inline-block;\n        font-size: 24px;\n    }\n\n    &:not(.-rounded) {\n        [class^='icon-'] {\n            margin-left: 8px;\n        }\n    }\n    &.-primary,\n    &.-primary2,\n    &.-primary3 {\n        // color: $color_white;\n        // background-color: $color_pumpkin;\n        // border-color: $color_pumpkin;\n\n        &:hover {\n            // background-color: $color_pumpkin_dark;\n            // border-color: $color_pumpkin_dark;\n        }\n\n        &.-alt {\n            // background-color: $color_cinder;\n        }\n    }\n    &.-primary2,\n    &.-primary3 {\n        // background-color: $color_cinder;\n        // border-color: $color_cinder;\n    }\n\n    &.-secondary {\n        font-weight: 400;\n        // color: $color_cinder;\n        // background-color: $color_white;\n        // border-color: $color_cinder;\n        transition: text-shadow 0.25s;\n\n        &:hover {\n            // text-shadow: 0 0 1px $color_black;\n        }\n    }\n    &.-borderless {\n        position: relative;\n        padding: 0;\n        font-weight: 400;\n        // color: $color_cinder;\n        background-color: transparent;\n        border: 0;\n        transition: 0.5s;\n\n        &:hover {\n            // text-shadow: 0 0 1px $color_black;\n            transition: 0.25s;\n        }\n\n        &.-bounce {\n            \n        }\n        @keyframes bounce {\n            0% {\n                width: 45%;\n            }\n            60% {\n                width: 150%;\n                transform: scale(1.1, 1);\n            }\n            80% {\n                width: 150%;\n                transform: scale(0.9, 1);\n            }\n            100% {\n                width: 150%;\n                transform: scale(1, 1);\n            }\n        }\n    }\n    &.-secondary2 {\n        position: relative;\n        padding-right: 5px;\n        // color: $color_cinder;\n        border: 0;\n        border-radius: 0;\n        &::before {\n            position: absolute;\n            top: 50%;\n            left: 0;\n            z-index: 0;\n            display: block;\n            width: 56px;\n            height: 56px;\n            margin-top: -28px;\n            content: '';\n            // background-color: $color_grey-6;\n            // border: 1px solid $color_cinder;\n            border-radius: 50px;\n            opacity: 0.03;\n        }\n        .c-btn {\n            padding-left: 33px;\n        }\n        .icon-shevron-top {\n            font-weight: 700;\n        }\n    }\n    &.-disabled {\n        // color: $color_grey-3;\n        pointer-events: none;\n        cursor: default;\n        // background-color: $color_grey;\n        // border: 1px solid $color_grey-2;\n    }\n    &.-loading {\n        [class^='icon-'] {\n            transform-origin: center;\n            animation: loading 1.5s infinite linear;\n        }\n    }\n    &.-alt {\n        /*\n                Przyciski występujacy pojedynczo, zawsze powinien posiadać paddingi w skali 8px.  Drugą miarą jego umieszczenia jest rozciągnięcie na całą szerokość gridu lub siatki wyznaczonej przez inne elementy interfejsu. Przycisk nie może nachodzić na inne elementy lub wychodzić poza tło.\n    \n                Przyciski występujące obok siebie, muszą mieć zdefiniowaną wagę ważności. Obszar separujący dla buttonów powinien być mierzony w skali 8px.\n    \n                Ikona, umieszczona jest zawsze w skali 8px od labelu tekstowego.\n            */\n        font-weight: 400;\n        // border-color: $color_grey-2;\n\n        &:hover {\n            // border-color: $color_cinder;\n        }\n    }\n    &.-add {\n        font-weight: 400;\n        // border: 1px solid $color_grey-4;\n\n        [class^='icon-'] {\n            margin-right: 10px;\n            margin-left: 0;\n        }\n\n        &:hover {\n            // color: $color_white;\n            // text-shadow: 0 0 1px $color_white;\n            // background-color: $color_cinder;\n        }\n    }\n    &.-rounded {\n        width: 56px;\n        height: 56px;\n        padding: 0;\n        // background-color: $color_pumpkin;\n        // border-color: $color_pumpkin;\n        &:hover {\n            // background-color: $color_pumpkin-dark;\n            // border-color: $color_pumpkin-dark;\n        }\n        &.-disabled {\n            // background-color: rgba($color_pumpkin, 0.2);\n            border-color: transparent;\n        }\n    }\n    &.-cart {\n        // color: $color_white;\n\n        .c-quantity {\n            // @include typo_aeonik_11-12;\n            position: absolute;\n            top: 0;\n            right: 0;\n            z-index: 1;\n            display: flex;\n            align-items: center;\n            justify-content: center;\n            min-width: 18px;\n            min-height: 18px;\n            font-weight: 700;\n            // color: $color_white;\n            // background-color: $color_cinder;\n            // border: 2px solid $color_white;\n            border-radius: 50px;\n            &.-empty {\n                display: none;\n            }\n        }\n        &.-disabled {\n            pointer-events: all;\n        }\n    }\n}\n\n/******************************************************/\n\n.c-link {\n    // @include typo_aeonik_14-20;\n    position: relative;\n    display: inline-block;\n    padding-bottom: 4px;\n    letter-spacing: 0.02em;\n    cursor: pointer;\n    transition: text-shadow 0.25s, color 100ms ease-in;\n\n    &::after {\n        position: absolute;\n        bottom: 4px;\n        left: 0;\n        display: block;\n        width: 100%;\n        height: 1px;\n        content: '';\n    }\n    &.-primary {\n        // color: $color_pumpkin;\n        &:hover {\n            // color: $color_pumpkin;\n            // text-shadow: 0 0 1px $color_pumpkin;\n        }\n        &::after {\n            // background-color: $color_pumpkin;\n        }\n    }\n    &.-secondary {\n        // color: $color_cinder;\n        &:hover {\n            // color: $color_cinder;\n            // text-shadow: 0 0 1px $color_cinder;\n        }\n        &::after {\n            // background-color: $color_cinder;\n        }\n    }\n    &.-alternative {\n        // color: $color_cinder;\n        &:hover {\n            // color: $color_cinder;\n            // text-shadow: 0 0 1px $color_cinder;\n        }\n    }\n    &.-disabled {\n        pointer-events: none;\n        opacity: 0.3;\n    }\n}\n","/* ==========================================================================\n\t Vars\n\t ========================================================================== */\n\n/* Colors */\n$color_white: #fff;\n$color_black: #000;\n$color_black2: #2E2A24;\n\n$color_cinder: #121212;\n$color_grey: #fbfbfb;\n$color_grey-2: #f4f4f4;\n$color_grey-3: #676f7e;\n$color_grey-4: #f0edea;\n$color_grey-5: #878787;\n$color_grey-6: #f8f8f8;\n$color_grey-7: #cdcdcd;\n$color_limestone_grey: #9ca3af;\n$color_light_beige: #c3c3c3;\n$color_pumpkin: #ff641e;\n$color_pumpkin-dark: #eb4900;\n$color_error_info: #e00000;\n$color_pearl_bush: #f5eee3;\n$color_parchment: #efe3d1;\n$color_bush: #00321e;\n$color_green: #1AB800;\n$color_success: #00ac67;\n$color_limestone_grey: #9ca3af;\n\n/* Fonts */\n$font_family-aeonik: 'Aeonik', sans-serif;\n\n/* Breakpoints */\n$breakpoints: (\n    xxxs: 320px,\n    xxs: 375px,\n    xs: 576px,\n    sm: 768px,\n    md: 992px,\n    lg: 1200px,\n    xl: 1440px,\n);\n\n/* Distances */\n$menu_height: 105px;\n$menu_height_mobile: 68px;\n\n/* Other */\n$image_path: '../../images';\n","//// media queries\n\n// media queries for above breakpoints\n// usage: @include respond-below(sm) {...}\n\n@use 'vars' as *;\n\n@mixin respond-below($breakpoint) {\n    @if map-has-key($breakpoints, $breakpoint) {\n        $breakpoint-value: map-get($breakpoints, $breakpoint);\n\n        @media (max-width: ($breakpoint-value - 1)) {\n            @content;\n        }\n    } @else {\n        @warn 'Invalid breakpoint: #{$breakpoint}.';\n    }\n}\n@mixin respond-above($breakpoint) {\n    @if map-has-key($breakpoints, $breakpoint) {\n        $breakpoint-value: map-get($breakpoints, $breakpoint);\n\n        @media (min-width: ($breakpoint-value)) {\n            @content;\n        }\n    } @else {\n        @warn 'Invalid breakpoint: #{$breakpoint}.';\n    }\n}\n\n// getting breakpoints values\n// usage: @include respond-below(sm) {...}\n\n@function breakpoint($breakpointName) {\n    @if map-has-key($breakpoints, $breakpointName) {\n        @return map-get($breakpoints, $breakpointName);\n    } @else {\n        @warn 'Invalid breakpoint: #{$breakpoint}.';\n    }\n}\n\n// visibility modifying classes\n// usage: class=\"-hideBelow-md\"\n\n@mixin hide-below() {\n    @each $key, $value in $breakpoints {\n        .-hideBelow-#{$key} {\n            @media (max-width: ($value - 1)) {\n                display: none;\n            }\n        }\n    }\n}\n\n@include hide-below();\n\n@mixin hide-above() {\n    @each $key, $value in $breakpoints {\n        .-hideAbove-#{$key} {\n            @media (min-width: ($value)) {\n                display: none;\n            }\n        }\n    }\n}\n\n@include hide-above();\n\n@mixin backdrop-filter-blur($blur, $color, $initValue) {\n    background-color: rgba($color, $initValue + 0.2);\n\n    @supports (\n        (-webkit-backdrop-filter: blur($blur)) or (backdrop-filter: blur($blur))\n    ) {\n        background-color: rgba($color, $initValue);\n        -webkit-backdrop-filter: blur($blur);\n        backdrop-filter: blur($blur);\n    }\n}\n\n@mixin placeholder {\n    &::-webkit-input-placeholder {\n        @content;\n    }\n    &:-moz-placeholder {\n        @content;\n    }\n    &::-moz-placeholder {\n        @content;\n    }\n    &:-ms-input-placeholder {\n        @content;\n    }\n}\n","/****** Elad Shechter's RESET *******/\n/*** box sizing border-box for all elements ***/\n*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\n\na {\n    color: inherit;\n    text-decoration: none;\n    cursor: pointer;\n}\n\nbutton {\n    padding: 0;\n    color: inherit;\n    cursor: pointer;\n    background-color: transparent;\n    border-width: 0;\n}\n\nfigure {\n    margin: 0;\n}\n\ninput::-moz-focus-inner {\n    padding: 0;\n    margin: 0;\n    border: 0;\n}\n\nul,\nol,\ndd {\n    padding: 0;\n    margin: 0;\n    list-style: none;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    margin: 0;\n    font-size: inherit;\n    font-weight: inherit;\n}\n\np {\n    margin: 0;\n}\n\ncite {\n    font-style: normal;\n}\n\nfieldset {\n    padding: 0;\n    margin: 0;\n    border-width: 0;\n}\n\ninput[type='search']::-ms-clear {\n    display: none;\n    width: 0;\n    height: 0;\n}\ninput[type='search']::-ms-reveal {\n    display: none;\n    width: 0;\n    height: 0;\n}\ninput {\n    background-color: transparent;\n    border: 0;\n    appearance: none;\n}\nsection {\n    max-width: 100vw;\n    overflow: hidden;\n}\n","/* ==========================================================================\n   Global\n   ========================================================================== */\n\n@use 'vars' as *;\n@use 'mixins' as *;\n\nhtml,\nbody {\n    width: 100